import axios from 'axios';

// xsrfrequest headerheader
axios.defaults.xsrfCookieName = 'csrfToken';
axios.defaults.xsrfHeaderName = 'x-csrf-token';

// 
axios.interceptors.response.use(
  res => {
    if (res.data.code !== 1) {
      throw new Error(res);
    } else {
      return res.data.data;
    }
  },
  error => {
    throw Error(error);
  }
);

